@font-face {
  font-family: pokeHollow;
  src: url("Pokemon Hollow.b1faf6ae.ttf");
}

@font-face {
  font-family: pokeSolid;
  src: url("Pokemon Solid.66546939.ttf");
}

@font-face {
  font-family: cardFont;
  src: url("Early GameBoy.61a60387.ttf");
}

* {
  box-sizing: border-box;
}

body {
  background-color: -webkit-linear-gradient(to right, #2bcff7, #00fbff);
  background-color: -moz-linear-gradient(to right, #2bcff7, #00fbff);
  background-color: linear-gradient(to right, #2bcff7, #00fbff);
  background: -webkit-linear-gradient(to right, #42b7ff, #5cfcff);
  background: -moz-linear-gradient(to right, #42b7ff, #5cfcff);
  background: linear-gradient(to right, #42b7ff, #5cfcff);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

header {
  display: inline-block;
}

.main__header_img {
  min-width: auto;
  width: 100%;
  margin-top: 2.2em;
}

.search__bar {
  width: 350px;
  height: 55px;
  background-color: #e5e5ff;
  border-radius: 25px;
  justify-content: center;
  align-items: center;
  margin: 1em auto;
  display: flex;
  position: relative;
  box-shadow: 0 2px 4px #00000040;
}

.search__bar input {
  width: -moz-fit-content;
  width: fit-content;
  height: 55px;
  color: #000;
  letter-spacing: 1px;
  text-indent: 5px;
  background: none;
  border: none;
  outline: none;
  flex-grow: 1;
  padding: 0 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.2em;
}

.search__bar:hover {
  color: #fff;
  box-shadow: 0 0 10px #2a3e7c;
}

.search__bar button {
  width: -moz-fit-content;
  width: fit-content;
  height: 55px;
  color: #e5e5ff;
  cursor: pointer;
  letter-spacing: 2px;
  background-color: #3c5aa6;
  border: none;
  border-radius: 0 25px 25px 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.2em;
  font-weight: bold;
  top: 0;
  right: 0;
}

.random__btn {
  width: -moz-fit-content;
  width: fit-content;
  height: 50px;
  color: #e5e5ff;
  cursor: pointer;
  letter-spacing: 2px;
  background-color: #3c5aa6;
  border: none;
  border-radius: 25px;
  margin-top: 1.5em;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.4em;
  font-weight: bolder;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.random__btn:hover {
  color: #fff;
  box-shadow: 0 0 10px #2a3e7c;
}

h1 {
  text-align: center;
  color: #3c5aa6;
  letter-spacing: 5px;
  text-shadow: 2px 2px 2px #0003;
  margin-top: .2em;
  font-family: pokeSolid;
  font-size: 2em;
  position: absolute;
  left: 0;
  right: 0;
}

#welcome__img {
  max-width: 75%;
  height: auto;
  margin: 0 auto;
  padding-top: .5em;
  display: block;
}

h2 {
  text-align: center;
  color: red;
  letter-spacing: 5px;
  text-shadow: -1px -1px #000, 1px -1px #000, -1px 1px #000, 1px 1px #000;
  max-width: 50%;
  margin: 0 auto;
  font-family: pokeSolid;
  font-size: 1.8em;
}

#error__img {
  max-width: 40%;
  height: auto;
  margin: 0 auto;
  padding-top: 3em;
  display: block;
}

.wrapper {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.pokeball {
  width: 60px;
  height: 60px;
  background-color: #fff;
  border: 3px solid;
  border-radius: 50%;
  margin-top: 10em;
  margin-left: 42%;
  animation: .8s linear infinite frames;
  display: none;
  position: absolute;
  overflow: hidden;
}

.pokeball:after {
  content: "";
  width: 60px;
  height: 30px;
  background-color: red;
  border-bottom: 4px solid;
  position: absolute;
  top: -4px;
}

.pokeball:before {
  content: "";
  width: 10px;
  height: 10px;
  z-index: 1;
  background-color: #fff;
  border: 4px solid;
  border-radius: 50%;
  position: absolute;
  inset: 50% 18px 18px 50%;
  transform: translate(-50%, -50%);
}

@keyframes frames {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.pokemon__card {
  -webkit-outline: 2px solid black;
  outline-offset: -2px;
  -webkit-outline-offset: -2px;
  text-align: center;
  background: #e5e5ff;
  border-radius: 25px;
  outline: 2px solid #000;
  grid-template: "name" 1fr
                 "number"
                 "image"
                 "height"
                 "weight"
                 "type"
                 "abilities"
                 / 1fr;
  gap: 1em;
  margin-bottom: 2em;
  padding: .5em;
  font-family: cardFont;
  display: grid;
  box-shadow: 0 2px 4px #00000040;
}

.pokemon__name {
  text-align: center;
  color: red;
  grid-area: name;
  place-self: center;
  font-size: 1.5em;
}

.pokemon__number {
  grid-area: number;
  place-self: center;
  margin-top: .8em;
  font-size: 1.2em;
}

.pokemon__image {
  width: 50%;
  height: auto;
  grid-area: image;
  place-self: center;
  margin-top: .8em;
}

.pokemon__type {
  white-space: nowrap;
  grid-area: type;
  place-self: center;
  margin-top: .8em;
}

.pokemon__height {
  white-space: nowrap;
  grid-area: height;
  place-self: center;
  margin-top: .8em;
}

.pokemon__weight {
  white-space: nowrap;
  grid-area: weight;
  place-self: center;
  margin-top: .8em;
}

.pokemon__abilities {
  white-space: wrap;
  grid-area: abilities;
  place-self: center;
  margin-top: .8em;
}

